import React from 'react'
import styled from 'react-emotion'

const Loading = styled('div')`
  margin-top: 40px;
  color: deeppink;
  text-align: center;
`

const Small = styled('div')`
  font-size: 11px;
`

export const Licences = ({ isLoading, error, data }) => {
  if (!isLoading) {
    return (
      <Loading>
        Loading...
        <Small>
          Please wait, this may take a while depending on the size of your
          package
        </Small>
      </Loading>
    )
  }

  return <div>{JSON.stringify(data)}</div>
}



